
.about{
background-color: #FFF5F6;

}
.about-container{

padding-bottom: 20px;
padding-top: 125px;

}

.how-its-made-title{
    margin: 0 auto;
color:  #D41443;
 font-family:  Titan One;
font-style: normal;
font-size: 34px;
line-height: calc(39/34);
text-align: center;
letter-spacing: 0.04em;
text-transform: uppercase;
margin-bottom: 26px;
@media screen and(min-width: 320px) {
    width:200px;
    text-align: center;

   }

}

.how-its-made-article{
 color:  #000000;
 font-family: DM Sans;
font-style: normal;
font-weight: 500;
font-size: 12px;
line-height: calc(19/12);
margin-top: 46px;
margin-bottom: 45px;
@media screen and(min-width: 320px) {
    width:220px;

   }
}
.how-its-made-text{
color: #907E82;
font-family: DM Sans;
font-style: normal;
font-weight: 500;
font-size: 12px;
line-height: calc(19/12);
background-image: url(../images/about/bg-milk-mob-1x.png);
background-repeat: no-repeat;
background-position: center;
background-size: cover;

margin-bottom: 17px;
}
.how-its-made-tradition{
margin: 0 auto;
color: #FFA5BA;
font-family: Titan One;
font-style: normal;
font-weight: normal;
font-size: 18px;
line-height: calc(21/18);
text-align: center;
text-transform: uppercase;
margin-bottom: 15px;
}

.read-more-button{
display: flex;
align-items:center;
justify-content:center;
background-color: #FFFFFF;
border-radius: 22px;
width: 176px;
height: 44px;
font-family: DM Sans;
font-style: normal;
font-weight: bold;
font-size: 14px;
line-height: calc(18/14);
text-align: center;
color: #D41443;
border: none;
cursor: pointer;
}

.read-more-button-icon{
    margin-left: 14px;
}
.how-its-made-cow-picture{
    width:100%;
    @media screen and(min-width: 320px) {
       width:280px;
       margin:0 auto;
      }
}

.how-its-made-text-wrapper{
    padding-left: 50px;
    padding-right: 50px;
}
